
import utils from '@/assets/js/utils.js'
const content = {
    template:'icon',
    key:utils.createRandomId(),
    basic:{
        title:'基本结构',
        desc:'渲染一个icon图标',
        html:{
            code:utils.htmlEncode(
                `
    <i o-template="icon" o-key='address' o-type="icon-address" ></i>
                `
            ),
            copy:
            `
            <i o-template="icon" o-key='address' o-type="icon-address" ></i>
            `
        },
        css:'',
        js:'',
    },
    demos:[
        {
            title:'自定义大小颜色',
            desc:'',
            src:'/static/osff_template/templates/icon/icon1.html',
            show:false,
            html:{
                code:utils.htmlEncode(
                    `
    <i o-template="icon" o-key='a' o-type="icon-address" o-size="100" o-color="#fa0"></i>

                    `
                ),
                copy:
                `
                <i o-template="icon" o-key='a' o-type="icon-address" o-size="100" o-color="#fa0"></i>
                `
            },
            css:'',
            js:'',
        },
        {
            title:'带交互的图标',
            desc:'在icon基础上，添加了点击事件，可用作按钮',
            src:'/static/osff_template/templates/icon/icon2.html',
            show:false,
            html:{
                code:utils.htmlEncode(
                    `
    <i  
        id="icon" 
        o-template="icon" 
        o-key='a' 
        o-type="icon-address" 
        o-size="100" 
        o-color="#fa0" 
        o-click="clickIcon">
    </i>

                    `
                ),
                copy:
                `
                <i  
                    id="icon" 
                    o-template="icon" 
                    o-key='a' 
                    o-type="icon-address" 
                    o-size="100" 
                    o-color="#fa0" 
                    o-click="clickIcon">
                </i>

                `
            },
            css:'',
            js:{
                code:
                `
    window.osff = {
        methods:{
            clickIcon:function(event){
                //点击时执行
                $('#icon').css({
                    color:'#0af'
                })
            }
        }
    }
   
                `,
           copy:
           `
           window.osff = {
                methods:{
                    clickIcon:function(event){
                        //点击时执行
                        $('#icon').css({
                            color:'#0af'
                        })
                    }
                }
            }
                       `
            },
        }
    ],
    api:{
        columns :[
            {
                title: '参数',
                key: 'params',
                dataIndex: 'params',
                width:150,
                scopedSlots: { customRender: 'params' },
            },
            {
                title: '说明',
                key: 'desc',
                dataIndex: 'desc',
                scopedSlots: { customRender: 'desc' },
            },
            {
                title: '是否必须',
                key: 'must',
                dataIndex: 'must',
                width:150,
                align:'center',
                scopedSlots: { customRender: 'must' },
            },
            {
                title: '默认值',
                key: 'default',
                dataIndex: 'default',
                width:150,
                scopedSlots: { customRender: 'default' },
            }
        ],
        data:[
            {
                params:'o-template',
                desc:'需要使用的模板名称',
                must:1,
                default:'无'
            },
            {
                params:'o-key',
                desc:'为每个模板设置一个唯一的识别码，防止同一个页面重复使用多个板产生互相干扰，使用图标的名称作为o-key，可提高代码可读性，例如 地址图标：o-key="address"',
                must:1,
                default:'无'
            },
            {
                params:'o-type',
                desc:'图标类型。遵循图标的命名规范 值：String,例：icon-address',
                must:1,
                default:'无'
            },
            {
                params:'o-size',
                desc:'设置图标的大小 值：任意数字，例：16',
                must:0,
                default:'无'
            },
            {
                params:'o-color',
                desc:'设置图标的颜色 ，值：rgba(x,x,x,x)|#xxx',
                must:0,
                default:'无'
            }
        ]
    },
    event:{
        columns :[
            {
                title: '事件名称',
                key: 'event',
                dataIndex: 'event',
                width:150,
                scopedSlots: { customRender: 'event' },
            },
            {
                title: '说明',
                key: 'desc',
                dataIndex: 'desc',
                scopedSlots: { customRender: 'desc' },
            },
            {
                title: '回调参数&返回值',
                key: 'return',
                dataIndex: 'return',
                width:250,
                scopedSlots: { customRender: 'return' },
            }
        ],
        data:[
            {
                event:'o-click',
                desc:'点击图标时触发，function(event), 事件必须在window.osff.methods对象上定义',
                return:'参数event：该点击事件的事件对象'
            }
        ]
    }
}

export default content